import "./App.css";
import Header from "./components/header/Header.component";
import Home from "./components/Home-Page/Home.component";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Checkout from "./components/check-out/checkout.component";
import SignUp from "./components/Sign-Up/SignUp.component";
import { auth } from "./firebase/firebase";
import { useStateValue } from "./CONTEX-API/stateprovider";
import { useEffect } from "react";
import Payments from "./components/Payments-Page/payment.component";
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";

const promise = loadStripe(
  "pk_test_51IFObzFrEwE4OD2ySHqkHTmLiEDjdx4hBbshIiJCP3SJz3lFR2EaWWYrfAQ7EFOFY2RpZaz8EImO7b3IqdubuSPH00qM4la4kF"
);

function App() {
  const [{}, dispatch] = useStateValue();

  useEffect(() => {
    auth.onAuthStateChanged((authUser) => {
      if (authUser) {
        // user was signed in or signed in
        dispatch({
          type: "SET_USER",
          user: authUser,
        });
      } else {
        dispatch({
          type: "SET_USER",
          user: null,
        });
      }
    });
  }, []);
  return (
    // BEM
    <Router>
      <div className="app">
        <Switch>
          <Route path="/login">
            <SignUp />
          </Route>
          <Route path="/payments">
            <Header />
            <Elements stripe={promise}>
              <Payments />
            </Elements>
          </Route>
          <Route path="/check-out">
            <Header />
            <Checkout />
          </Route>
          <Route path="/">
            <Header />
            <Home />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
