import React, { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import { auth } from "../../firebase/firebase";
import "./signup.styles.css";

function SignUp() {
  const history = useHistory();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const signIn = (e) => {
    e.preventDefault();
    auth.signInWithEmailAndPassword(email, password).then((auth) => {
      history.push("/");
    });
  };

  const SignUp = (e) => {
    e.preventDefault();
    auth
      .createUserWithEmailAndPassword(email, password)
      .then((auth) => {
        // it successfully created a neq user with
        // email and passwor
        history.push("/");
      })
      .catch((error) => alert(error.message));
    // do some fancy firebase signup shitttt...
  };
  return (
    <div className="login">
      <Link to="/">
        <img
          className="login__logo"
          src="https://www.freepnglogos.com/uploads/amazon-png-logo-vector/amazon-png-logo-vector-1.png"
          alt="Logo"
        />
      </Link>

      <div className="login__container">
        <h1 className="form-label-h1">Sign-in</h1>
        <form action="#">
          <h5>E-mail</h5>
          <input
            type="text"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />

          <h5>Password</h5>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />

          <button
            type="submit"
            onClick={signIn}
            className="login__signInButton"
          >
            Sign In
          </button>
        </form>

        <p>
          By continuing, you agree to Amazon clone's Conditions of Use and
          Privacy Notice.
        </p>

        <small>New to Amazon?</small>
        <button onClick={SignUp} className="login__signUpButton">
          Create your Amazon Account
        </button>
      </div>
    </div>
  );
}

export default SignUp;
